/* Custom CSS */

html, body {
    overflow: visible !important;
    width: auto;
    height: auto;
    font-family: "Red Hat Display", sans-serif;
    color: rgb(8, 17, 88);
    min-width: unset !important;
}

body #zmmtg-root {
    width: auto !important;
    height: auto !important;
    position: inherit !important;
}

#zmmtg-root .footer {
    box-sizing: unset;
}

.profile-container .form-control:disabled {
    background-color: transparent;
}

.profile-header-title {
    height: 0;
}


/* For react inline edit component */
/* these make sure it can work in any text element */
.tab-inner-content .transcript_row .inline-text {
    margin: 0;
}

.tab-inner-content .transcript_row .inline-text .inline-text_copy {
    margin: 0;
}

.inline-text_copy--active,
.inline-text_input--active {
  font: inherit;
  color: inherit;
  text-align: inherit;
  padding: 0;
  background: none;
  border: none;
  border-bottom: 1px dashed #999999;
  outline: none;
  width: 100% !important;
}

.inline-text_copy--active {
  cursor: pointer;
}

.transcript_row .inline-text_copy--hidden,
.transcript_row .inline-text_input--hidden {
  display: none;
}

.inline-text_input--active {
  border-bottom: 1px solid #666666;
  text-align: left;
}
