}

.top-title {
    color: #32325d;
}

.navbar-light .navbar-nav .active>.nav-link i {
    color: #3255ff;
}

.total-avatar {
    background: #3154ff !important;
    position: relative;
    top: -12px;
}

.custom-tabel .thead-light th {
    color: #000;
    background: transparent;
}

.tb-title {
    color: #000;
    font-weight: 500;
    font-size: 1.1em;
}

.triangle i {
    font-size: 23px;
    position: relative;
}

.total-record-duration {
    background-image: linear-gradient(to right, #3153ff, #5c50ff, #794dff, #9148ff, #a743fd);
}

.information-title {
    font-size: 2.5rem;
    margin: 0.5rem 0;
    display: block;
}

.total-record-duration p,
.total-record-duration span,
.total-record-duration .card-title {
    color: #fff;
}

.sm-icon {
    position: relative;
    top: 9px;
}

.recording-title span {
    color: #3255ff;
    font-size: 17px;
}

i.fas.fa-bookmark.text-info {
    color: #3255ff !important;
}

h1.main-page-title {
    position: relative;
    display: inline-block;
    color: #fff;
    top: -54px;
}

.all-participant-outer-row {
    margin-bottom: 4rem;
}

.potential-condidtes-table tbody.list td {
    padding: 1.9em;
}

.page-item.active .page-link {
    background-color: #3256ff;
    border-color: #3256ff;
}

ul.left-custom-tabs.nav.nav-tabs {
    border: none;
}

.zoom-meeting-top-title {
    font-size: 1.5em;
    margin-top: 11px;
