    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    background: white;
    box-shadow: 0 0 2rem 0 rgba(136, 152, 170, 0.15);
    border-radius: 0.375rem;
    padding: 2rem 1.5rem;
}

.participants-row .tb-title {
    font-size: 14px;
    margin-left: 0.5em;
}

h2.BigTitle {
    font-size: 2.5rem;
    margin-bottom: 2.5rem;
}

.tab-inner-content {
    padding-top: 3rem;
    padding-bottom: 4rem;
}

.tab-inner-content span {
    font-weight: 700;
    font-size: 1rem;
    display: inline-block;
    margin-bottom: 1rem;
}

.left-custom-tabs .nav-link {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 1.2rem;
    padding: 0.7rem 1rem;
    margin-right: 1rem;
    background: transparent !important;
    border-radius: 0 !important;
}

.left-custom-tabs a.active.nav-link {
    color: #3256ff !important;
    border-bottom: 2px solid #3256ff !important;
    border-top: 0 !important;
    border-left: 0 !important;
    border-right: 0 !important;
}

.left-custom-tabs .nav-link:hover {
    border-color: transparent !important;
}

.custom-card {
    background: #fff;
    border-bottom: 4px solid #3256ff;
    padding: 1.5rem 1rem 1rem;
    box-shadow: -1px 0px 10px rgba(0, 0, 0, 0.1);
    position: relative;
    margin-bottom: 1rem;
}

.custom-card .action-buttons {
    position: absolute;
    top: 40px;
    right: 20px;
    padding: 5px 10px;
    background: #fff;
    box-shadow: 1px 1px 4px #8a8a8a;
    border-radius: 5px;
}

.custom-card .action-buttons p {
    margin-bottom: 0;
}

.custom-card-body i {
    position: absolute;
    right: 20px;
    cursor: pointer;
}

.custom-card-body span {
    font-weight: 700;
    font-size: 14px;
}

.custom-card-body p {
    font-size: 16px;
    line-height: 22px;
    margin-top: 0.5rem;
    font-weight: 500;
    margin-bottom: 0.5rem;
}

.custom-card-body .comment-text a {
    color: #4a4a4a;
}

.custom-card-body .comment-text a:hover {
    color: #233dd2;
}

span.custom-card-footer {
    color: #b4b4b4;
    font-size: 14px;
    font-weight: 500;
}

.highlight-card {
    border-color: #f6b4f1;
}

.custom-sidebra-header ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    list-style: none;
    background: #3256ff;
    padding: 0.9rem 0;
}

.custom-sidebra-header ul li {
    font-weight: 500;
    color: #bec9ff;
    text-transform: capitalize;
    text-align: center;
    width: 33.3333%;
}

li.li-active {
    color: #fff !important;
}

.tab-inner-content h4 {
    font-size: 1.3rem;
}

span.transcript-time strong {
    color: #3356ff;
}

span.transcript-time {
    display: block;
    padding: 0;
    font-weight: 500;
    color: #959595;
    margin-top: 1.2rem;
    margin-bottom: 0.5rem;
}

span.sentiment-title {
    margin-top: 0.5rem;
    font-weight: normal;
    margin-bottom: 0.3em;
}

ul.sentiment-progress-bar {
    margin: 0;
    display: -webkit-flex;
    display: flex;
    position: relative;
    list-style: none;
    padding: 0;
}

ul.sentiment-progress-bar {
    margin: 0;
    display: -webkit-flex;
    display: flex;
    position: relative;
    list-style: none;
    padding: 0;
}

ul.sentiment-progress-bar li:last-child {
    position: absolute;
    right: 28%;
    top: 13px;
}

ul.sentiment-progress-bar li:first-of-type {
    background: #eb7f81;
    width: 33.333%;
    height: 10px;
}

span.transcript-user-title {
    margin-top: 0;
    font-size: 15px;
    margin-left: 0.5rem;
    width: 100%;
    margin-bottom: 0;
    margin-left: 0;
}

ul.sentiment-progress-bar li:nth-child(2) {
    background: #ede255;
    width: 33.333%;
    height: 10px;
}

ul.sentiment-progress-bar li:nth-child(3) {
    background: #a5cc4f;
    width: 33.333%;
    height: 10px;
}

ul.sentiment-progress-bar li:last-child span {
    margin: 0;
    font-weight: normal;
    font-size: 14px;
}

ul.sentiment-progress-bar li:last-child span:after {
    position: absolute;
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 6px 10px 6px;
    border-color: transparent transparent #000000 transparent;
    left: 4px;
    top: -8px;
}

p.color-para span {
    padding: 0 5px;
    margin: 0;
    display: inline;
    display: initial;
    color: #081158;
    font-weight: normal;
}

.transcript_row.row {
    margin-bottom: 7rem;
}

.comment-box-wrapper textarea {
    width: 100%;
}

.no-comments {
    color: red;
    padding: 10px;
    box-shadow: 1px 1px 8px;
    border-radius: 5px;
}

.no-comments h4 {
    color: red;
}
