.buttons-group {
  display: flex;
  flex-direction: column;
  align-items: center
}

.buttons-group__comment-btn {
  border: 0 none;
  border-radius: 2px;
  box-shadow: 1px 1px 4px #888888;
  z-index: 500;
  margin-right: 0 !important;
}

.buttons-group__down-arrow-tip {
  width: 12px;
  height: 12px;
  margin-top: -2px;
  background: #212529;
  z-index: 500;
  clip-path: polygon(0% 0%, 50% 50%, 100% 0%);
}

.sidenav.fixed-left+.main-content .highlit-button-group {
  margin-left: -62px;
}

.navbar-vertical.navbar-expand-xs.fixed-left+.main-content .highlit-button-group {
  margin-left: -62px;
}

@media (min-width: 1200px) {
  .g-sidenav-pinned .sidenav.fixed-left+.main-content .highlit-button-group {
    margin-left: -250px;
  }
}
